import React, { useCallback, useEffect, useState } from 'react'
import { BrowserRouter } from 'react-router-dom';
import ScrollToTop from './ScrollToTop';
import './index.css'
import { BlocksQuestion } from './components/BlocksQuestion';
import { useDispatch, useSelector } from 'react-redux';
import { fillAllBlock, updateWarn } from './redux/actions';
import { InfoProgress } from './components/InfoProgress';
import { FailWindow } from './components/FailWindow';
import { TimingComponent } from './components/TimingComponent';
import { START_TIME, STOP_TIME } from './redux/types';
import info_img_on from './img/info.png';
import info_img_off from './img/info1.png';
import { InfoWindow } from './components/InfoWindow';

let options = [
  {value: "1", answer: "One"},
  {value: "2", answer: "Two"},
  {value: "3", answer: "Three"},
  {value: "4", answer: "Four"},
  {value: "5", answer: "Five"},
  {value: "6", answer: "Six"},
  {value: "7", answer: "Seven"},
  {value: "8", answer: "Eight"},
  {value: "9", answer: "Nine"},
  {value: "10", answer: "Ten"},
  {value: "11", answer: "Eleven"},
  {value: "12", answer: "Twelve"},
  {value: "13", answer: "Thirteen"},
  {value: "14", answer: "Fourteen"},
  {value: "15", answer: "Fifteen"},
  {value: "16", answer: "Sixteen"},
  {value: "17", answer: "Seventeen"},
  {value: "18", answer: "Eighteen"},
  {value: "19", answer: "Nineteen"},
  {value: "20", answer: "Twenty"},
  {value: "30", answer: "Thirty"},
  {value: "40", answer: "Forty"},
  {value: "50", answer: "Fifty"},
  {value: "60", answer: "Sixty"},
  {value: "70", answer: "Seventy"},
  {value: "80", answer: "Eighty"},
  {value: "90", answer: "Ninety"},
  {value: "100", answer: "One hundred"},
  {value: "1000", answer: "One thousand"},
  {value: "1000000", answer: "One million"},
  {value: "1000000000", answer: "One billion"}
]
let time_control = ""
let time_global = ""
let warn_global = ""

function App() {
  const dispatch = useDispatch()
  const warning = useSelector(s => s.app.user.warn_lvl)
  let time = useSelector(s => s.app.user.time)
  const [infoToggle, setInfoToggle] = useState(false)

  const timeTickStart = () => {
    time_control = setInterval(() => {
      if(+time_global >= 1){
        dispatch({type: START_TIME})
      }else{
        dispatch(updateWarn())
        dispatch({type: STOP_TIME})
      }

      if(warn_global>2) timeTickStop()

    }, 1000)
  }

  const timeTickStop = () => {
    clearInterval(time_control)
    dispatch({type: STOP_TIME})
  }

  const checkIdentical = useCallback((block, right) => { // Проверка повтора
    const val_block = Math.floor(Math.random() * (31 - 0)) + 0 // Генератор

    if(options[val_block].answer !== right.answer) { // Проверка правильного ответа
      if(block.length !== 0){ // Проверка пустоты для первого вхождения
        block.every(b => (b !== options[val_block].answer)) // Проверка повтора ответов
        ?block.push(options[val_block].answer) 
        :checkIdentical(block, right) // Если попался такой же ответ, то генерируется ещё раз
        
      }else block.push(options[val_block].answer)
    }
    else checkIdentical(block, right) // Если попался такой же ответ, то генерируется ещё раз
  }, [])

  const fillBlock = useCallback(() => {

    let right = [options[Math.floor(Math.random() * (31 - 0)) + 0],
                   options[Math.floor(Math.random() * (31 - 0)) + 0],
                   options[Math.floor(Math.random() * (31 - 0)) + 0]]

    const block = []
    for (let i = 0; i < 3; i++) {
      block.push([])
      for (let j = 0; j < 4; j++) {
        checkIdentical(block[i], right[i])
      }
    }

     // Кидаем правильные ответы в массив с заменой в рандомное место
    block[0][Math.floor(Math.random() * (4 - 0)) + 0] = right[0].answer
    block[1][Math.floor(Math.random() * (4 - 0)) + 0] = right[1].answer
    block[2][Math.floor(Math.random() * (4 - 0)) + 0] = right[2].answer


    dispatch(fillAllBlock([[false, false, false], [false, false, false],
                           [right[0].value, block[0], right[0].answer],
                           [right[1].value, block[1], right[1].answer],
                           [right[2].value, block[2], right[2].answer]]))

  }, [dispatch, checkIdentical])

  useEffect(() => {
    fillBlock()
  }, [fillBlock])

  useEffect(() => time_global = time, [time])
  useEffect(() => warn_global = warning, [warning])

  return (
    <BrowserRouter>
      <div className="global-container">
        <ScrollToTop />
        <InfoProgress />

        {infoToggle&&<InfoWindow />}
        {warning>2&&<FailWindow fillBlock={fillBlock} />}

        <TimingComponent />

        <BlocksQuestion checkIdentical={checkIdentical} options={options} timeTickStart={timeTickStart} timeTickStop={timeTickStop} />
        {(!infoToggle||warning<3)&&<img onClick={() => setInfoToggle(!infoToggle)} className="info_button" src={info_img_on} alt=""/>}
        {(infoToggle||warning>2)&&<img onClick={() => setInfoToggle(!infoToggle)} style={{zIndex: 100}} className="info_button" src={info_img_off} alt=""/>}
      </div>
    </BrowserRouter>
  );
}

export default App;
