import {ADD_NEW_BLOCK, FILL_ALL, UPDATE_WARN, UPDATE_SCORE, RELPAY_GAME, START_TIME, STOP_TIME} from './types'

const initState = {
    user: {
        warn_lvl: 0,
        score: 0,
        time: 3
    },
    blocks: []
}

const noteReducer = (state = initState, action) => {
    switch (action.type) {
        case FILL_ALL:
            return {
                ...state, 
                blocks: action.payload
            }
        case ADD_NEW_BLOCK:
            return {
                ...state, 
                blocks: [...state.blocks.filter((b, i) => i !== 0), action.payload]
            }
        case UPDATE_WARN:
            return {
                ...state, 
                user: {
                    ...state.user,
                    warn_lvl: +state.user.warn_lvl + 1
                }
            }
        case UPDATE_SCORE:
            return {
                ...state, 
                user: {
                    ...state.user,
                    score: +state.user.score + 1
                }
            }
        case START_TIME:
            return {
                ...state, 
                user: {
                    ...state.user,
                    time: +state.user.time - 1
                }
            }
        case STOP_TIME:
            return {
                ...state, 
                user: {
                    ...state.user,
                    time: 3
                }
            }
        case RELPAY_GAME:
            return initState
        default:
            return state
    }
}

export default noteReducer