import {ADD_NEW_BLOCK, FILL_ALL, RELPAY_GAME, UPDATE_SCORE, UPDATE_WARN} from './types'

export const fillAllBlock = (value) => ({type: FILL_ALL, payload: value})
export const addNewBlock = (value) => ({type: ADD_NEW_BLOCK, payload: value})
export const updateWarn = () => ({type: UPDATE_WARN})
export const updateScore = () => ({type: UPDATE_SCORE})
export const replayGame = () => ({type: RELPAY_GAME})