import React from 'react'
import { useSelector } from 'react-redux';

export function InfoProgress() {
    const score = useSelector(s => s.app.user.score)
    const warning = useSelector(s => s.app.user.warn_lvl)

    return (
        <div className="info-process">
          <p>{score}</p>
          {warning>0&&<img alt="" src={require("./../img/warning1.png")['default']} />}
          {warning>1&&<img alt="" src={require("./../img/warning1.png")['default']} />}
        </div>
    );
}
