import React from 'react'
import { useDispatch, useSelector } from 'react-redux';
import './style.scss'
import play_img from '../img/play.png'
import { replayGame } from '../redux/actions';

export function FailWindow({fillBlock}) {
    const score = useSelector(s => s.app.user.score)
    const dispatch = useDispatch()

    return (
        <div className="fail-window">
            <div className="container">
                <p>SCORE: {score}</p>
                <img onClick={() => {dispatch(replayGame()); fillBlock();}} className="play" src={play_img} alt=""/>
            </div>
        </div>
    );
}
