import React from 'react'
import './style.scss'

export function InfoWindow() {

    return (
        <div className="info-container">
            <div className="container">
                <h1>Что делать?</h1>
                <p>Это бесконечный тест, который заставит вас выучить числа на английском. 
                    На табло появляется цифра и варианты её произношения. Нужно нажать на тот ответ, 
                    который вы считаете правильным. На ответ даётся 3 секунды и 3 попытки. Для большего эффекта произносить слова вслух.
                </p>
            </div>
        </div>
    );
}
