import React, { useState } from 'react'
import {  useDispatch, useSelector } from 'react-redux';
import { addNewBlock, updateScore, updateWarn } from '../redux/actions';
import './style.scss'

export function BlocksQuestion({checkIdentical, options, timeTickStart, timeTickStop}) {
    const blocks = useSelector(s => s.app.blocks)
    const warn_lvl = useSelector(s => s.app.user.warn_lvl)
    const dispatch = useDispatch()
    const [nextAnim, setNextAnim] = useState(false)

    const answerTest = (allOpt, valOpt) => {

        if(allOpt[2] === valOpt){
            setNextAnim(true)

            setTimeout(() => {
                setNextAnim(false)
                const block = []
                const right = options[Math.floor(Math.random() * (20 - 0)) + 0]
                for (let j = 0; j < 4; j++) {
                    checkIdentical(block, right)
                }
                block[Math.floor(Math.random() * (4 - 0)) + 0] = right.answer
    
                dispatch(updateScore())
                dispatch(addNewBlock([right.value, block, right.answer]))
                timeTickStop()
                warn_lvl<3&&timeTickStart()
            }, 180);
        }else{
            dispatch(updateWarn())
            timeTickStop()
            console.log(warn_lvl)
            warn_lvl<2&&timeTickStart()
        }
    }

    return (
        <div className="blocks-container">
            <div className="wrapper">
                {blocks.map((b, i) => <div key={i} style={nextAnim?{animation: "animbl"+(i+1)+" 0.2s 1"}:{}} className={"block-"+(i+1)+" block"}>
                    <div style={nextAnim?{animation: "animblH"+(i+1)+" 0.2s 1"}:{}} className="main_value">{b[0]}</div>
                    <div onClick={() => {(i===2)&&(answerTest(b, b[1][0]))}} className="answer">{b[1][0]}</div>
                    <div onClick={() => {(i===2)&&(answerTest(b, b[1][1]))}} className="answer">{b[1][1]}</div>
                    <div onClick={() => {(i===2)&&(answerTest(b, b[1][2]))}} className="answer">{b[1][2]}</div>
                    <div onClick={() => {(i===2)&&(answerTest(b, b[1][3]))}} className="answer">{b[1][3]}</div>
                    </div>)}
            </div>
        </div>
    );
}
